.App {
  text-align: center;
}

.myAlbumsHeader {
}

.albumListContainer {
  display: flex;
  flex-flow: row wrap;
  margin: 0% 10%;
  padding: 2% 6%;
}

.albumThumbAndName {
  margin: 1%;
  width: 30%;
  height: 40%;
  overflow: hidden;
}

.albumThumbLink{
  height: 100%;
  width: 100%;
  text-decoration: none;
  color: rgb(81, 81, 81);
}

.albumThumbLink:hover {
  color: rgb(126, 126, 126);
}

.thumbnail {
  width: 100%;
  height: 100%;
  object-fit: contain; /*wasn't necessary for this but may be need for diff. asp. ratios*/
  border-radius: 4%;
}

/*  E A C H    A L B U M   */
.main {
  margin: 0;
  padding: 0;
  display: flex;
}

.hide {
  z-index: -1;
  background-color: black;
  opacity: 0.8;
}

.selectedAlbumHeader {
  display: flex;
  padding: 2%;
}

.albumNameHeader {
  position: relative;
  margin: auto;
}

.leftArrowBox {
  color: rgb(28, 28, 28);
  position: absolute;
  font-weight: bold;
  text-decoration: none;
}

.leftArrowBox:hover {
  color: rgb(126, 126, 126);
}

.linksAndPhotos {
  display: flex;
  height: 80%;
  padding-top: 2%;
}

.albumsLinksContain{
  border: 1px solid gray;
  border-left: none;
  display: flex;
  flex-flow: column wrap;
}

.albumLinks {
  margin: 10px 20px 15px 20px;
  color: rgb(28, 28, 28);
  text-transform: capitalize;
  text-decoration: none;
  font-weight: bold;
}

.albumLinks:hover {
  color: rgb(126, 126, 126);
}

.photosListContainer{
  border-top: 1px solid gray;
  display: flex;
  flex-flow: row wrap;
  margin: 0;
  padding: 0 6%;
}

.photoDiv {
  margin: 0 1%;
  width: 30%;
  height: 50%;
  object-fit: contain;
}

.photoDiv img {
  margin: 0;
  height: 80%;
  width: 100%;
  object-fit: contain;
}

.photoDiv img:hover {
  opacity: 0.7;
  cursor: grab;
}

.photoDiv h4 {
  margin: 0;
  height: 10%;
  width: 100%;
  padding-top: 5px;
}

.photoDiv h4:hover {
  color: rgb(123, 123, 123);
  cursor: grab;
}

h4, h1 {
  text-transform: capitalize;
}

.expandedPhotoContainer {
  display: flex;
  position: absolute;
  left: 0;
  z-index: 1;
  width: 100%;
  backdrop-filter: blur(5px);
}

.expandedPhoto {
  margin: 0 auto;
  height: 100%;
  width: 60%;
  padding: 0 10% 20% 10%;
}

.expandedPhoto img {
  max-width: 90%;
  max-height: 50%;
}

.expandedPhoto h1 {
  color: rgb(163, 162, 162);
  margin: 1% 5% 1% 5%;
  font-weight: 300;
}